<template>
  <div class="image-slider">
    <div class="image-slider-slide" :style="{ transform: `translateX(${-currentSlide * 100}%)` }">
      <div v-for="(picture, index) in pictures" :key="index" class="image-slider-item">
        <img :src="picture.src" :alt="picture.alt" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pictures: [
        {
          src: require('@/assets/images/carousel1.png'),
          alt: 'Picture 1',
        },
        {
          src: require('@/assets/images/carousel1.png'),
          alt: 'Picture 2',
        },
        {
          src: require('@/assets/images/carousel1.png'),
          alt: 'Picture 3',
        },
      ],
      currentSlide: 0,
      timer: null,
      slideInterval: 3000, // Set the desired interval for automatic sliding in milliseconds
    };
  },
  mounted() {
    this.startSlideTimer();
  },
  beforeUnmount() {
    this.stopSlideTimer();
  },
  methods: {
    startSlideTimer() {
      this.timer = setInterval(() => {
        this.nextSlide();
      }, this.slideInterval);
    },
    stopSlideTimer() {
      clearInterval(this.timer);
      this.timer = null;
    },
    nextSlide() {
      this.currentSlide = (this.currentSlide + 1) % this.pictures.length;
    },
  },
};
</script>

<style>
.image-slider {
  position: relative;
  overflow: hidden;
}

.image-slider-slide {
  display: flex;
  transition: transform 0.5s ease;
}

.image-slider-item {
  flex-shrink: 0;
  width: 100%;
}

.image-slider-item img {
  width: 100%;
  height: auto;
}
</style>





