            博士紅感謝全省合作客戶</h1>
        <p class="mb-8 text-lg font-normal text-center text-red-500 lg:text-xl sm:px-16 lg:px-48 dark:text-gray-400">
            （歡迎全省客戶全國廠商來圖刊登公司有權修改及下架不宜內容）</p>

        <div class="slider  " :style="{ transform: `translateX(${translateX}%)` }">
            <div v-for="(slide, index) in slides" :key="index" class="slide shadow-md grid grid-cols-2 bg-white ">
                <div class="slide-item border-2 " v-for="(image, subIndex) in slide.images" :key="subIndex">
                    <img :src="image.src" :alt="image.alt" class="slide-image">
                    <div class="slide-caption">{{ image.caption }}</div>
                </div>

            </div>
        </div>
        <div class="relative mt-5 px-4 py-16 mx-auto border-4 shadow-xl sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-0 lg:py-0">
            <img class="object-cover w-full h-full sm:h-96  " src="@/assets/images/meterial5.jpg" alt="" />
           <p class="text-center py-2">馬可烘培蛋糕坊</p>
        </div>
        
