</template>
  
<script>
export default {
    data() {
        return {
            slides: [
                {
                    images: [
                        { src: require('@/assets/images/meterial3.jpg'), alt: "Slide 1 Image 1", caption: "中正中醫診所" },
                        { src: require('@/assets/images/meterial2.jpg'), alt: "Slide 1 Image 2", caption: "左腳右腳按摩" },
                        { src: require('@/assets/images/meterial4.jpg'), alt: "Slide 1 Image 3", caption: "馬可烘培蛋糕坊" },
                        { src: require('@/assets/images/meterial1.jpg'), alt: "Slide 1 Image 4", caption: "大山檳榔" },
                    ],
                    title: "Slide 1 Title",
                    description: "Slide 1 Description",
                },
                // {
                //     images: [
                //         { src: require('@/assets/images/4.jpeg'), alt: "Slide 1 Image 1", caption: "Image 1 Caption" },
                //         { src: require('@/assets/images/4.jpeg'), alt: "Slide 1 Image 2", caption: "Image 2 Caption" },
                //         { src: require('@/assets/images/4.jpeg'), alt: "Slide 1 Image 3", caption: "Image 3 Caption" },
                //         { src: require('@/assets/images/4.jpeg'), alt: "Slide 1 Image 4", caption: "Image 4 Caption" },
                //     ],
                //     title: "Slide 2 Title",
                //     description: "Slide 2 Description",
                // },
                // Add more slides here
            ],
            currentIndex: 0,
            translateX: 0,
            slideInterval: null,
            intervalDuration: 5000, // Duration between slide transitions (in milliseconds)
        };
    },
    mounted() {
        this.startSlider();
    },
    methods: {
        startSlider() {
            this.slideInterval = setInterval(this.nextSlide, this.intervalDuration);
        },
        nextSlide() {
            this.currentIndex = (this.currentIndex + 1) % this.slides.length;
            this.translateX = -this.currentIndex * 100; // Adjust the value based on your slide width and spacing
        },
    },
    beforeUnmount() {
        clearInterval(this.slideInterval);
    },
};
</script>
  
